import React from "react"

import Layout from "../components/layout"
import ClientSlider from "../components/body/slider"
import WhatsappApi from "../components/contact/wpp"
import Scroll from "../components/body/scroll"
import SEO from "../components/seo"
import Typewriter from "typewriter-effect"

const IndexPage = () => {
  return (
    <>
      <Layout>
        <SEO title="Allto Digital" />
        <div className={"page-header home"}>
          <h1>
            Digitalizao e automao <br /> de processos
            <Typewriter
              options={{
                strings: [
                  "industriais",
                  "administrativos",
                  "financeiros",
                  "de RH",
                  "e outras reas de back-office",
                ],
                autoStart: true,
                loop: true,
              }}
            />
          </h1>
          <br />
        </div>

        <WhatsappApi />

        <ClientSlider />

        <Scroll />
      </Layout>
    </>
  )
}

export default IndexPage
